@media screen and (min-width:80px) and (max-width: 720px) {

  #div-canto {
    padding: 0px;
    margin: 0px;
    margin-top: 60px;
    align-items: center;
    text-align: center;
    /* Centraliza o texto horizontalmente */
    position: absolute;
    justify-content: center;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
  }
  #sect-video {
    justify-content: center;
    top: 0px;
    position: relative;
    width: 100%;
    height: 660px;
    overflow: hidden;
  }
  
  .video-container {
  transform: skewY(10deg);
  
  margin-top: -50px;
    position: relative;
    height:400px ;
    /* 16:9 aspect ratio */
    overflow: hidden;
    background: #000;
  }
  
  .video-container video,
  .video-container object,
  .video-container embed {
    
  transform: skewY(-10deg);
    position: absolute;
    top: -50%;
    justify-content: center;
    left: -217%;
    width: auto;
    height: auto;
  }
  
  #new-canto {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 460px;
    height: 470px;
    /* background-color: rgb(104, 104, 104); */
  }
  
  .content-logo {
    display: none;
    padding-bottom: 50px;
    padding-left: 53px;
    padding-right: 53px;
  }
  .content-logo img {
    width: 100%;
  }
  
  #new-canto h1 {
    justify-content: center;
    width: 100%;
    padding-bottom: 10px;
    font-size: 15px;
    /* Tamanho do texto */
    color: #f0f0f0;
    /* Cor do texto */
    margin: 0;
    /* Remove margens padrão */
  }
  
  .p-text {
    justify-content: center;
    width: 100%;
    padding-bottom: 10px;
    font-size: 15px;
    /* Tamanho do texto */
    color: #f0f0f0;
    /* Cor do texto */
    margin: 0;
    /* Remove margens padrão */
  }
  
  .h1-new {
    text-align: center;
    /* Centraliza o texto horizontalmente */
  }
  
  /* --------------------------------------- */
  
  .custom-button {
    width: 240px;
    display: flex;
    margin:0 auto;
    /* Exibe o conteúdo como flexbox para alinhamento */
    align-items: center;
    
    justify-content: center;
    /* Centraliza verticalmente o conteúdo */
    background-color: #ff4655;
    /* Cor de fundo do botão */
    color: #f0f0f0;
    /* Cor do texto */
    text-decoration: none;
    /* Remove sublinhado */
    padding: 10px 20px;
    /* Espaçamento interno */
    border-radius: 25px;
    /* Borda arredondada */
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    /* Transição suave para o hover e sombra */
    font-weight: bold;
    /* Negrito */
    font-size: 16px;
    /* Tamanho da fonte */
    box-shadow: 0 8px 15px rgba(196, 31, 31, 0.4);
    /* Sombra esfumaçada abaixo do botão */
  }
  
  .custom-button:hover {
    background-color: #e53e44;
    /* Cor de fundo ao passar o mouse */
    box-shadow: 0 12px 20px rgba(196, 31, 31, 0.4);
    /* Sombra mais intensa no hover */
  }
  
  
  .custom-button__content {
    height: 50px;
    width: 100%;
    display: flex;
    /* Flexbox para ícone e texto */
    align-items: center;
    /* Alinha o conteúdo */
    justify-content: center;
  }
  
  .custom-button__icon {
    background-color: #20191a;
    /* Cor de fundo ao passar o mouse */
    border-radius: 50%;
    width: 26px;
    /* Largura do ícone */
    height: 26px;
    /* Altura do ícone */
    padding: 10px;
    margin-right: 8px;
    /* Espaço entre o ícone e o texto */
  }
  
  .custom-button__icon svg {
    width: 100%;
    /* Tamanho do SVG para preencher o contêiner */
    height: 100%;
    /* Tamanho do SVG para preencher o contêiner */
    fill: #f0f0f0;
    /* Cor do ícone */
  }
  
  .p-button {

    font-size: 15px;
  }
}


